import React, { useState } from "react";
// import SideNav from "../Components/SideNav/SideNav";
// import TopNav from "../Components/TopNav/TopNav";
import Admin from "../Pages/Login/Login";
import Dashboard from "../Pages/Dashboard/Dashboard";
import Home from "../Pages/HomePage/HomePage";
import Play_Card from "../Pages/PlayCard/PlayCard";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import PublicRoute from "../AppRoutes/PublicRoute/PublicRoute";
import PrivateRoute from "../AppRoutes/PrivateRoute/PrivateRoute";

function AppRoutes() {

  return (
    <>
      <Router>
        <Switch>
          <PublicRoute exact path="/admin" component={Admin}></PublicRoute>
          <Route exact path="/" component={Home}></Route>
          <Route  path="/play-card" component={Play_Card}></Route>
          <PrivateRoute
            exact
            path="/dashboard"
            component={Dashboard}
          ></PrivateRoute>
        
        </Switch>
      </Router>
    </>
  );
}

export default AppRoutes;
