import Loading from "../Components/Loader/Loader";
import { useHistory } from "react-router-dom";
import { SERVER_URL } from "../config/serverUrl";
import axios from "axios";
import { createContext, useState, useEffect } from "react";
export const PlayCardContext = createContext();

const PlayCardContextProvider = (props) => {
  const history = useHistory();
  const [isLoading, setLoading] = useState(false);
  const [product, setProduct] = useState([]);
  const [uniquePlayCard, setUniquePlayCard] = useState("");
  const [airTableStatus, setAirTableStatus] = useState("");
  const [currentPage, setCurrentPage] = useState();
  const [totalPage, setTotalPage] = useState();
  //
  const getAllProduct = async (page) => {
    setLoading(true);
    const res = await axios.get(
      `${SERVER_URL}/playCardsAdmin/GetAllPlayCards?limit=10&page=${page}`
    );
    setProduct(res?.data?.data?.cards);
    setLoading(false);
    setTotalPage(res?.data?.data?.totalPages);
    setCurrentPage(res?.data?.data?.page);
  };

  const Refresh = async () => {
    // code for post refresh API
    setLoading(true);
    const res = await axios.post(
      `${SERVER_URL}playCardsAdmin/ResetAllPlayCards`
    );
    setLoading(false);
    getAllProduct(1);
    // console.log(res, "refresh --->>");
  };

  const AirTable = async () => {
    // here code for AirTable API
    setLoading(true);
    const res = await axios.post(`${SERVER_URL}playCardsAdmin/SyncPlayCards`);
    setAirTableStatus(res.data?.type);
    setLoading(false);
  };
  return (
    <div>
      <PlayCardContext.Provider
        value={{
          product,
          isLoading,
          uniquePlayCard,
          AirTable,
          Refresh,
          airTableStatus,
          getAllProduct,
          currentPage,
          totalPage,
        }}
      >
        {props.children}
      </PlayCardContext.Provider>
    </div>
  );
};

export default PlayCardContextProvider;
