import React, { useState, useEffect } from "react";
import styles from "./PlayCard.module.scss";
import card1 from "../../Assets/8.png";
import gif from "../../Assets/loader_gif.gif";
import { Row, Button, Col } from "reactstrap";
import Confetti from "react-confetti";
import "./override1.scss";
import { useHistory } from "react-router-dom";
import { SERVER_URL } from "../../config/serverUrl";
import axios from "axios";
const PlayCard = () => {
  const history = useHistory();
  const [confetti, setConfetti] = useState(true);
  const [loading, setLoading] = useState(false);
  const [uniqueCard, setUniquePlayCard] = useState("");
  setTimeout(() => {
    setConfetti(false);
  }, 5500);
  const GenerateCard = async () => {
    setLoading(true)
    const res = await axios.get(`${SERVER_URL}playCards/GetNewPlayCard`);
    setUniquePlayCard(res?.data?.data?.URL);
    setLoading(false);
  };
  useEffect(() => {
    GenerateCard();
  }, [])

  return (
    <>
      <div className={styles.home_page_container}>
        {loading ? (
          <div className={styles.gif_container}>
            <img src={gif} alt="Loading" width="280px" />
          </div>
        ) : (
          <>
            {confetti ? <Confetti/> : null}
            <Row className={`${styles.main_container} gx-0`}>
              <Col sm={12} xs={12} md={12}>
                <img src={uniqueCard} className={styles.card_img} alt="card" />
              </Col>
              <Col sm={12} xs={12} md={12} className={styles.button}>
                <Button onClick={e => history.push("/")}>Done</Button>
              </Col>
            </Row>
          </>
        )}
      </div>
    </>
  );
};

export default PlayCard;
